<template>
  <div id="blog">
    <div id="list">
      <div class="block" v-for="blog in currentblogs" :key="blog['name']">
        <div class="title">{{blog["name"] }}</div>
        <div class="view" :id="blog['name']">
          <div hidden="true">{{ view(blog["download_url"],blog["name"])}}</div>
        </div>
        <div class="viewall">
          <button @click="gotoreader(blog['name'])">More</button>
        </div>
      </div>
      <div id="ind" v-if="pageNum!=1">
        <button
          class="pageind"
          v-for="index in pageNum"
          :key="index"
          @click="list(blogs, Blogs_per_Page, index, len)"
          :style="Getindcolor(index,currentPage)"
        >{{index}}</button>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import showdown from "showdown";
export default {
  props: {
    UserName: {
      type: String,
      required: true
    },
    ProjectName: {
      type: String,
      required: true
    },
    Blogs_per_Page: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      blogs: [],
      currentblogs: [],
      editormd: new showdown.Converter({ tables: true, strikethrough: true }),
      pos: 0,
      len: 0,
      pageNum: 1,
      currentPage: 1
    };
  },
  mounted() {
    this.initlist(
      this.$props.UserName,
      this.$props.ProjectName,
      this.$props.Blogs_per_Page
    );
  },
  methods: {
    initlist(UserName, ProjectName, Blogs_per_Page) {
      axios
        .get(
          "http://api.github.com/repos/" +
            UserName +
            "/" +
            ProjectName +
            "/contents/MarkDown"
        )
        .then(Response => {
          this.blogs = Response.data;
          this.len = this.blogs.length;
          this.pageNum = Math.ceil(this.len / Blogs_per_Page);
          for (let i = 0; i < Blogs_per_Page && i < this.len; i++) {
            this.currentblogs.push(this.blogs[i]);
          }
        });
    },
    list(Data, Blogs_per_Page, current, len) {
      this.currentblogs = [];
      console.log(Data);
      for (
        let i = 0;
        i < Blogs_per_Page && i + (current - 1) * Blogs_per_Page < len;
        i++
      ) {
        this.currentblogs.push(Data[i + (current - 1) * Blogs_per_Page]);
      }
      this.currentPage = current;
    },
    async view(url, id) {
      console.log(url);
      axios.get(url).then(Response => {
        document.getElementById(id).innerHTML = this.$data.editormd.makeHtml(
          Response["data"]
        );
      }); //Lazy
    },
    gotoreader(id) {
      this.$router.push({ path: "article", query: { pid: id } });
    },
    Getindcolor(ind, currentPage) {
      if (ind == currentPage) {
        return "background-color:var(--main_color);color:var(--second_color);";
      }
    }
  }
};
</script>
<style scoped>
#list {
  padding-top: 10px;
  padding-bottom: 10px;
  width: 800px;
  min-height: 450px;
  border-radius: 5px;
  margin: -80px auto;
  background-color: var(--second_color);
  z-index: 2;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 20px;
  box-shadow: 0 8px 16px darkgray;
}

.block {
  z-index: 3;
  position: relative;
  height: 280px;
  width: 750px;
  opacity: 0.9;
  border-radius: 5px;
  margin: 20px 25px;
  box-shadow: 0 8px 16px darkgray;
}
.block:hover {
  animation: bigger 0.3s;
  animation-fill-mode: forwards;
}
.block div {
  padding: 5px 10px;
  margin: 0px;
  width: 730px;
  z-index: 3;
}

.block .title {
  position: relative;
  height: 40px;
  font-size: 15px;
  color: #fff;
  border-radius: 5px 5px 0px 0px;
  background-color: var(--main_color);
  text-overflow: ellipsis;
}
.block .view {
  position: relative;
  height: 220px;
  font-size: 16px;
  border-radius: 0px 0px 5px 5px;
  background-color: #fff;
  text-overflow: ellipsis;
  overflow: hidden;
}
.block .viewall {
  margin-top: -120px;
  position: relative;
  bottom: 0px;
  height: 110px;
  width: 730px;
  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0.3),
    rgba(255, 255, 255, 1)
  );
}
.block .viewall button {
  float: right;
  position: relative;
  right: 30px;
  top: 30px;
  margin: 0px auto;
}

#ind {
  height: 50px;
  min-width: 100px;
  bottom: 30px;
}

.pageind {
  width: 30px;
  padding-left: 10px;
  padding-right: 10px;
  height: 30px;
  font-size: 20px;
  margin: 10px 10px;
  color: var(--main_color);
  background-color: var(--second_color);
  border-radius: 3px;
  float: left;
}

button {
  font-size: 15px;
  border: none;
  transition: box-shadow 0.5s ease-out;
  height: 40px;
  width: 80px;
  border-radius: 30px;
  background-color: var(--main_color);
  color: var(--second_color);
}

button:hover {
  box-shadow: 0 8px 16px darkgray;
  cursor: pointer;
}

@keyframes bigger {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.02);
  }
}
</style>

