import Vue from 'vue'
import VueRouter from 'vue-router'
import index from './components/index'
import article from './components/article'
Vue.use(VueRouter)
const UserName = "Archcy", //UserName
    ProjectName = "Archcy.github.io", //Project Name
    Blogs_per_Page = 4 //default is 5
const router = new VueRouter({ // 注册路由
    mode: 'history',
    base: './',
    routes: [
        { path: '/', component: index, props: { UserName: UserName, ProjectName: ProjectName, Blogs_per_Page: Blogs_per_Page } }, //主页
        //{ path: '/category', component: category }, //分类
        { path: '/article', component: article, props: { UserName: UserName, ProjectName: ProjectName } }, //文章
    ]
})

export default router