<template>
  <div id="reader">
    <div id="rcontent">
        
    </div>
    <button id="goback" @click="goback()">Back to Blogs</button>
  </div>
</template>
<script>
import axios from "axios"
import showdown from "showdown";
export default {
  props: {
    UserName: {
      type: String,
      required: true
    },
    ProjectName: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      urlhead: "https://raw.githubusercontent.com/",
      urltail: "/master/MarkDown/",
      editormd: new showdown.Converter({ tables: true, strikethrough: true })
    };
  },
  mounted() {
    this.readdata(
      this.$data.urlhead +
        this.$props.UserName +
        "/" +
        this.$props.ProjectName +
        this.$data.urltail +
        this.$route.query.pid
    );
  },
  methods: {
    async readdata(url) {
      axios.get(url).then(Response => {
        document.getElementById(
          "rcontent"
        ).innerHTML = this.$data.editormd.makeHtml(Response["data"]);
      });
    },
    goback()
    {
      this.$router.go(-1);
    }
  }
};
</script>
<style scoped>
#reader {
  padding: 30px 40px;
  width: 700px;
  min-height: 500px;
  border-radius: 5px;
  margin: -80px auto;
  background-color: var(--second_color);
  overflow-y: auto;
  overflow-x: auto;
  box-shadow: 0 8px 16px darkgray;
}

#goback {
  font-size: 15px;
  width: 160px;
  height: 40px;
  float: right;
  position: fixed;
  right: 30px;
  bottom: 20px;
  border-radius: 30px;
  background-color: var(--main_color);
  color: var(--second_color);
  border: none;
}

#goback:hover{
  cursor:pointer;
  transition: box-shadow 0.5s ease-out;
}
</style>

